.recommended {
	display: flex;
	flex-direction: column;
	align-items: left;
	margin: 2rem 0 2rem 4rem;
	display: flex;
	gap: 10px;
	text-align: left;

	&__img {
		width: 350px;
		height: auto;
	}
}

@media (max-width: 560px) {
	.grid__movies {
		grid-template-columns: 100%;
	}
}
